.about {
    padding-top: 60px;
    &__grid {
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        gap: 90px;
    }
    &__subtitle {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        letter-spacing: 0.05em;
        color: #1C7E66;
        margin-bottom: 18px;
    }
    &__descr {
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #686868;
    }
    &__img {
        width: 100%;
        object-fit: cover;
    }
}